.note {
  width: 250px;
  min-height: 100px;
  background-color: $color-1;
  position: absolute;
  top: 10px;
  left: 10px;
  cursor: pointer;
  padding: 10px;
  box-shadow: 5px 5px 7px -3px rgba(0, 0, 0, 0.68);
  // font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-family: "Indie Flower", cursive;
  font-size: 24px;
  .notedate {
    font-size: 14px;
    margin-bottom: 10px;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }
  .noteTextArea {
    width: 100%;
    resize: none;
    border-color: rgba(255, 255, 255, 0.479);
    border-style: solid;
    border-width: 1px;
    outline: none;
    background-color: #dad2b3;
  }

  .noteColorArea {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    cursor: default;
    .color {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      cursor: pointer;
      border-width: 2px;
      border-color: white;
      border-style: solid;
    }
    .color1 {
      background-color: $color-1;
    }
    .color2 {
      background-color: $color-2;
    }
    .color3 {
      background-color: $color-3;
    }
    .color4 {
      background-color: $color-4;
    }
    .color5 {
      background-color: $color-5;
    }
    .btnConfirmAddNote {
      background-color: white;
      width: 34px;
      height: 34px;
      border-radius: 50%;
      cursor: pointer;
      border: white solid 2px;
      color: grey;
      outline: none;
      display: flex;
      justify-content: space-around;
      align-items: center;
    }
    .btnConfirmAddNote:hover {
      background-color: grey;
      color: white;
      border: grey solid 2px;
    }
  }
  .noteOperations {
    margin-top: 20px;
    font-size: 14px;
    text-align: right;
    .noteOperationsInner {
      display: flex;
      justify-content: flex-end;
      .noteEdit {
        margin-right: 10px;
      }
      .noteDeleteClicked {
        background: rgb(27, 121, 55);
        color: white;
        border-radius: 3px;
        width: 100%;
        .noteDeleteUndo {
          width: 100%;
          display: block;
          text-align: center;
          padding: 15px 0;
        }
      }
    }
  }
}
