@import "_vars.scss";
@import "_note.scss";
html,
body {
  margin: 0;
  padding: 0;
  box-sizing: 0;
  background: #ffffff;
}

// general
.hide {
  display: none;
}
.hideColor {
  display: none;
}

.beforeDelete {
  // border: rgb(230, 127, 127) 2px solid;
  animation: fadeOutToDelete 3.1s ease-out;
}

// notes container
.notesContainer {
  width: 100%;
  height: 100vh;
  position: relative;
}

//operations
.operations {
  position: fixed;
  top: 10px;
  right: 50px;
  z-index: 100;
  width: 250px;
  // background: rgb(133, 69, 69);
  align-items: center;
  .notie {
    width: 100%;
    // background-color: burlywood;
    // margin-bottom: 10px;
    text-align: center;
    font-family: "Indie Flower", cursive;
    font-size: 50px;
    color: rgb(156, 94, 13);
  }
  .searchAndAdd {
    display: flex;
    // background: #000;
    .search {
      margin-right: 20px;
      .searchInput {
        padding: 12px;
        font-size: 18px;
        width: 150px;
        outline: none;
        border: rgb(3, 102, 86) solid 3px;
        border-radius: 25px;
        color: rgb(12, 194, 164);
        box-shadow: 5px 5px 7px -3px rgba(0, 0, 0, 0.68);
      }
    }
    .addnote {
      height: 50px;
      width: 50px;
      background: rgb(3, 102, 86);
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      color: white;
      font-size: 18px;
      box-shadow: 5px 5px 7px -3px rgba(0, 0, 0, 0.68);
      cursor: pointer;
    }
  }
  .colorSearch {
    margin-top: 10px;
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .colorCircle {
      width: 40px;
      height: 40px;
      box-shadow: 5px 5px 7px -3px rgba(0, 0, 0, 0.68);
      border-radius: 20px;
      cursor: pointer;
    }
    .colorCircle1 {
      background-color: $color-1;
    }
    .colorCircle2 {
      background-color: $color-2;
    }
    .colorCircle3 {
      background-color: $color-3;
    }
    .colorCircle4 {
      background-color: $color-4;
    }
    .colorCircle5 {
      background-color: white;
    }
  }
}

@keyframes fadeOutToDelete {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
